import React from 'react';

const ContactForm = () => (
  <div style={{ maxWidth: '800px', margin: '0 auto' }}>
    <h3 className="mb-4 text-center">  </h3>
    <form>
      <div className="mb-3">
        <label htmlFor="name" className="form-label"></label>
        <input type="text" className="form-control" id="name" placeholder="  " />
      </div>
      <div className="mb-3">
        <label htmlFor="email" className="form-label"> </label>
        <input type="email" className="form-control" id="email" placeholder="name@example.com" />
      </div>
      <div className="mb-3">
        <label htmlFor="phone" className="form-label"></label>
        <input type="tel" className="form-control" id="phone" placeholder="+380..." />
      </div>
      <div className="mb-3">
        <label htmlFor="callbackDate" className="form-label">   </label>
        <input type="text" className="form-control" id="callbackDate" placeholder=" " />
      </div>
      <div className="mb-3">
        <label htmlFor="comment" className="form-label"></label>
        <textarea className="form-control" id="comment" rows="4" placeholder="   ..."></textarea>
      </div>
      <button type="submit" className="btn btn-primary w-100" style={{ backgroundColor: 'black', border: 'none' }}>
        
      </button>
    </form>
  </div>
);

export default ContactForm;
