                                                                                            import React from "react";
import "./AllNews.css";
import first_news from './img/1.png'
import second_news from './img/2_2.jpg'
import third_news from './img/3_3.png'
import fourth_news from './img/4.png'
import fifth_news from './img/5.jpg'
import sixth_news from './img/6.jpg'
import seventh_news from './img/7.jpg'
import eights_news from './img/8.jpg'
import ninth_news from './img/9.jpg'
import ten_news from './img/1.jpg'

const newsData = [
  {
    id: 1,
    title: "   2025   ",
    date: "2025-01-15",
    image: first_news,
    description: "         2025."
  },                                                  
  {
    id: 2,
    title: "     ",
    date: "2025-02-01",
    image: second_news,
    description: "     ,   '  2025 ."
  },
  {
    id: 3,
    title: "'    2025",
    date: "2025-02-10",
    image: third_news,
    description: "              ."
  },
  {
    id: 4,
    title: "    ",
    date: "2025-02-20",
    image: fourth_news,
    description: "  11             ."
  },
  {
    id: 5,
    title: "     ",
    date: "2025-03-01",
    image: fifth_news,
    description: "          .   !"
  },
  {
    id: 6,
    title: "   ",
    date: "2025-03-10",
    image: sixth_news,
    description: "        2025.    ."
  },
  {
    id: 7,
    title: "   ",
    date: "2025-03-20",
    image: seventh_news,
    description: "      -    ."
  },
  {
    id: 8,
    title: "    ",
    date: "2025-04-01",
    image: eights_news,
    description: "    :   ,        ."
  },
  {
    id: 9,
    title: "    ",
    date: "2025-04-10",
    image: ninth_news,
    description: "              ."
  },
  {
    id: 10,
    title: "    ",
    date: "2025-04-20",
    image: ten_news,
    description: "         ,   ."
  },

];

function AllNews() {
  return (
    <div className="all-news container mt-4">
      <h2 className="mb-4"> </h2>
      {newsData.map((news) => (
        <div key={news.id} className="news-row mb-4">
          <img src={news.image} alt={news.title} className="news-img" />
          <div className="news-text">
            <h4>{news.title}</h4>
            <p>{news.description}</p>
            <div className="d-flex justify-content-between align-items-center">
              <small className="text-muted">{news.date}</small>
              <button className="btn btn-sm btn-black"></button>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

export default AllNews;
